@font-face {
    font-family: 'Poppins';
    src: url('../fonts/Poppins-Regular.ttf');
}

@font-face {
    font-family: 'font1';
    src: url('../fonts/ChakraPetch-Bold.ttf');
}

@font-face {
    font-family: 'font2';
    src: url('../fonts/PoetsenOne-Regular.ttf');
}

@font-face {
    font-family: 'font3';
    src: url('../fonts/Proxima-Nova-Font.otf');
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    scroll-behavior: smooth;
    font-family: 'Poppins';
}




/* ========== CENTER TEXT START ========== */
.center-text {
    p {
        width: fit-content;
        color: #000000;
        font-size: 42px;
        font-family: 'font1';
        font-weight: 500;
        line-height: 55px;
        position: relative;
        text-transform: uppercase;
        margin-bottom: 10px;
        overflow: hidden;
    }
}

.center-text p::after {
    position: absolute;
    width: 100%;
    height: 4px;
    /* background-color: green; */
    background: linear-gradient(90deg, #fe9418 0%, #f5634b 100%);

    content: '';
    bottom: 0;
    transition: all 0.3s ease;
    left: 0;
    transform: translateX(-70%);
}

.center-text p:hover::after {
    transform: translateX(0%);
}

/* ========== CENTER TEXT END ========== */



/* ========== NAVAR START ========== */


nav {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 20px 5%;
    z-index: 1111;
    top: 0px;
    background-color: #000000;
    box-shadow: 1px 1px 50px 1px rgba(128, 128, 128, 0.63);
    position: sticky;
    backdrop-filter: blur(15px);

    .mobile-menu {
        display: none;
        color: white;
    }

    .logo {
        p {
            font-size: 30px;
            font-weight: bolder;
            text-transform: capitalize;
            color: white;
            font-family: font1;
        }
    }

    section {
        display: flex;

        ul {
            display: flex;
            align-items: center;
            justify-content: end;

            li {
                list-style: none;
                margin: 0 10px;
                padding: 10px 10px;
                overflow-x: hidden;

                .links {
                    text-transform: capitalize;
                    text-decoration: none;
                    color: wheat;
                    padding: 10px 5px;
                    font-weight: 800;
                    letter-spacing: 1.2px;
                }
            }
        }
    }
}

.active {
    background-color: #f5634b;
    border-radius: 5px;
    color: white !important;

}


/* ========== NAVAR END ========== */



/* ========== MAIN SECTION START ========== */
.main-img-container {
    /* background-color: #CBA590; */
    background-color: #000000;
    background-image: url('../Assests/download\ \(3\).png');
    background-position: bottom right;
    background-repeat: no-repeat;
    background-size: contain;
    padding: 20px 2%;
    height: 700px;

    .text-area-main {
        display: flex;
        height: 100%;
        justify-content: center;
        flex-direction: column;

        h6 {
            color: #FFFFFF;
            text-transform: uppercase;
            letter-spacing: 3px;
            margin-bottom: 20px;
            font-family: 'Courier New', Courier, monospace;
        }

        h1 {
            /* font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; */
            color: white;
            font-size: 88px;
            font-family: serif;
            font-family: 'font2';
        }

        p {
            margin-top: 20px;
            color: white;
            font-family: Verdana, Geneva, Tahoma, sans-serif;

        }
    }
}

/* ========== MAIN SECTION END ========== */




/* ========== CONTACT SECTION START ========== */
.contact {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding: 50px 2%;

    .child {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        height: 200px;

        .logo {
            height: 30%;
            font-size: 50px;
            margin-bottom: 10px;
        }

        .txt {
            height: 70%;

            /* background-color: red;    */
            h6 {
                color: #000000;
                font-size: 20px;
                margin-bottom: 10px;
                font-weight: 500;
                letter-spacing: 1px;
                line-height: 29px;
                text-transform: capitalize;
            }

            p {
                font-size: 16px;
                color: #000000;
                opacity: 0.7;
            }
        }
    }


}

/* ========== CONTACT SECTION END ========== */





/* ========== PARALLAX SECTION START ========== */
.parallax {
    background: linear-gradient(rgba(0, 0, 0, 0.521), rgba(0, 0, 0, 0.521)), url("../Assests/download\ \(5\).png");
    /* background: linear-gradient(rgba(0, 0, 0, 0.521), rgba(0, 0, 0, 0.521)), url("../Assests/download.png"); */
    min-height: 400px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 5%;
    font-size: 30px;
    line-height: 40px;
    color: white;
    font-weight: bolder;
    text-transform: capitalize;

    /* font-family: Arial, Helvetica, sans-serif; */
    h2 {
        font-family: 'font3';
    }

}

/* ========== PARALLAX SECTION END ========== */






/* ========== PRODUCT SECTION START ========== */
.product {
    display: flex;
    padding: 50px 5%;
    /* background-color: #CBA590; */
    flex-direction: column;
    align-items: center;
    justify-content: center;

    .product-slider {
        margin-top: 15px;
        height: 400px;
        width: 100%;

        .img {
            width: 100%;
            height: 400px;

            img {
                width: 100%;
                height: 100%;
                object-fit: fill;
            }
        }
    }

}

/* ========== PRODUCT SECTION END ========== */





/* ========== ABOUT US SECTION START ========== */
.aboutus {
    display: grid;
    gap: 20px;
    grid-template-columns: repeat(2, 1fr);
    padding: 50px 2%;

    /* background-color: red; */
    .child {
        display: flex;
        justify-content: center;
        flex-direction: column;
        height: 520px;
        width: 100%;

        img {
            width: 100%;
            height: 100%;
            border-radius: 15px;
            object-fit: cover;
        }

        h4 {
            color: black;
            text-transform: uppercase;
            font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
            letter-spacing: 1.2px;
            margin-bottom: 10px;
        }

        p {
            text-transform: capitalize;
            opacity: 0.7;
            margin-bottom: 10px;
        }

        .btn-1 {
            width: fit-content;
            background-color: black;
            margin-top: 10px;
            color: #FFFFFF;
            display: flex;
            font-weight: bold;
            letter-spacing: 1px;
            padding: 12px 25px;
            border-radius: 5px;
            text-decoration: none;
            text-transform: capitalize;
            position: relative;
            overflow: hidden;
            z-index: 1;

        }

        .btn-1::before {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            transition: all 0.5s ease;
            background-color: green;
            top: 0;
            left: 0;
            z-index: -1;

            transform: translateX(-100%);
        }

        .btn-1::after {
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            transition: all 0.5s ease;
            background-color: green;
            top: 0;
            left: 0;
            z-index: -1;

            transform: translateX(100%);
        }

        .btn-1:hover::before {
            transform: translateX(-50%) rotate(180deg);
        }

        .btn-1:hover::after {
            transform: translateX(+50%) rotate(180deg);
        }

    }
}

/* ========== ABOUT US SECTION END ========== */





/* ========== SLIDER START ========== */
.slick-slide>div {
    margin: 0px 10px;
}

/* ========== SLIDER END ========== */






/* ========== TRETEMENT SECTION START ========== */
.tretement {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 50px 2%;

    .tretement-parent {
        display: grid;
        width: 100%;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
        margin-top: 15px;

        .child {
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            height: 300px;
            background-color: white;
            z-index: 1;
            overflow: hidden;
            border-radius: 15px;
            box-shadow:
                0 1px 1px hsl(0deg 0% 0% / 0.075),
                0 2px 2px hsl(0deg 0% 0% / 0.075),
                0 4px 4px hsl(0deg 0% 0% / 0.075),
                0 8px 8px hsl(0deg 0% 0% / 0.075),
                0 16px 16px hsl(0deg 0% 0% / 0.075);
            text-transform: capitalize;
            position: relative;

            h3 {
                font-weight: bolder;
                letter-spacing: 1.2px;
            }
        }

        .child::before, .Mehdi::before, .eyeBrow::before, .facial::before, .facial::before, .haitCut::before {
            transition: all 0.5s ease;
            content: '  ';
            /* background: linear-gradient(rgba(0, 0, 0, 0.356), rgba(0, 0, 0, 0.356)), url('../Assests/bg.jpeg'); */
            background: linear-gradient(90deg, #fe9418 0%, #f5634b 100%);
            background-position: center;
            background-size: cover !important;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;
            transform: translateY(-95%);
            border-radius: 15px;
        }

        .child:hover::before, .Mehdi:hover::before, .eyeBrow:hover::before, .haitCut:hover::before, .facial:hover::before, .makup:hover::before {
            transform: translateY(0%);
        }

        .child:hover, .Mehdi:hover, .eyeBrow:hover, .haitCut:hover, .facial:hover, .makup:hover {
            color: white;
        }
    }
}

/* ========== TRETEMENT SECTION END ========== */






/* ========== GALLARY SECTION START ========== */
.Gallary {
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 50px 2%;
    background-color: rgba(0, 0, 0, 0.459);

    .images {
        margin-top: 15px;
        display: grid;
        gap: 20px;
        grid-template-columns: repeat(4, 1fr);
        width: 100%;

        img {
            height: 300px;
            width: 100%;
            object-fit: cover;
            border: 3px solid whitesmoke;
        }

        img:nth-child(6) {
            background-color: red;
            grid-column: span 2;
        }
    }
}

/* ========== GALLARY SECTION END ========== */






/* ========== FOOTER SECTION START ========== */
.footer {
    background-color: #000000b7;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    flex-direction: column;
    color: white;

    img {
        width: auto;
        border-radius: 50%;
        height: 100px;
    }

    p {
        font-size: 16px;
        margin-top: 7px;
        text-transform: capitalize;
        letter-spacing: 1px;
    }

    h5 {
        font-size: 14px;
        text-transform: capitalize;
        font-weight: 700;
        letter-spacing: 1.5px;
        margin-bottom: 5px;
    }

    .social-media {
        display: flex;

        a {
            text-decoration: none;
            margin-left: 10px;

            img {
                width: 24px;
                height: auto;
            }
        }
    }
}

/* ========== FOOTER SECTION END ========== */




/* ===== about us page ===== */
.banner {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 240px;
    background: url('../Assests/banner.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    p {
        font-size: 60px;
        font-weight: 400;
        line-height: 78px;
        color: #000000;
        text-transform: capitalize;
        font-family: font2;
    }
}


/* MOBILE SCREEN MEDIA QUERY START*/
@media screen and (max-width: 767px) {

    nav {
        padding: 5px 2%;

        .logo {
            p {
                font-size: 25px;
            }
        }

        .mobile-menu {
            display: flex;
            align-content: end;
            justify-content: end;
            font-size: 30px;
        }

        section {
            display: none;
            width: 100%;

            ul {
                width: 100%;
                grid-column: span 2 /3;
                flex-direction: column;

                li {
                    margin: 10px 0;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    a {
                        width: 100%;
                    }
                }
            }
        }

        section.open {
            display: flex;
        }
    }


    .main-img-container {
        height: 100vh;

        .text-area-main {
            justify-content: start;

            h1 {
                font-size: 50px;
            }

            p {
                font-size: 11px;
            }
        }
    }

    .tretement {
        .tretement-parent {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    .aboutus {
        grid-template-columns: repeat(1, 1fr);

        .child {
            height: 350px;
        }
    }

    .contact {
        grid-template-columns: repeat(1, 1fr);
    }

    .parallax {
        font-size: 25px;
    }

    .product {
        .items {
            grid-template-columns: repeat(1, 1fr);
        }
    }

    .product {
        justify-content: center;

        .product-slider {
            height: 300px;

            .img {
                height: 300px;
            }
        }
    }

    .Gallary {
        .images {
            grid-template-columns: repeat(2, 1fr);

            img {
                height: 200px;
                width: 100%;
                object-fit: cover;
                border: 3px solid whitesmoke;
            }

            img:nth-child(5) {
                display: none;
            }
        }
    }

    .banner {
        height: 140px;

        p {
            font-size: 50px;
        }
    }




}

/* MOBILE SCREEN MEDIA QUERY END*/








/* LEPTOP SCREEN MEDIA QUERY START */
@media screen and (min-width: 768px) and (max-width: 1023px) {
    nav {
        padding: 5px 2%;

        .logo {
            p {
                font-size: 25px;
            }
        }

        .mobile-menu {
            display: flex;
            align-content: end;
            justify-content: end;
            font-size: 30px;
        }

        section {
            display: none;
            width: 100%;

            ul {
                width: 100%;
                flex-direction: column;


                li {
                    margin: 10px 0;
                    width: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    a {
                        width: 100%;
                    }
                }
            }
        }

        section.open {
            display: flex;
        }
    }

    .main-img-container {
        height: calc(100vh - 80px);
        background-position: bottom right;
        background-size: contain;
        background-size: 60%;

        .text-area-main {
            h1 {
                font-size: 60px;
            }

            p {
                font-size: 15px;
                width: 60%;
            }
        }
    }

    .product {
        justify-content: center;

        .product-slider {
            height: 350px;

            .img {
                height: 300px;
            }
        }
    }

    .contact {
        grid-template-columns: repeat(2, 1fr);
    }

    .product {
        .items {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    .tretement {
        .tretement-parent {
            grid-template-columns: repeat(2, 1fr);
        }
    }

    .Gallary {
        .images {
            grid-template-columns: repeat(3, 1fr);

            img {
                height: 200px;
                width: 100%;
                object-fit: cover;
                border: 3px solid whitesmoke;
            }

            img:nth-child(5) {
                display: none;
            }
        }
    }


}

/* LEPTOP SCREEN MEDIA QUERY END */